grees);\n}\n\n@mixin icon-flip($horiz, $vert, $rotation) {\n  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=#{$rotation});\n  -webkit-transform: scale($horiz, $vert);\n      -ms-transform: scale($horiz, $vert);\n          transform: scale($horiz, $vert);\n}\n\n@mixin input-status-color {\n  &, &.focused {\n    &.success {\n      border: solid 1px var(--success);\n      input, .selected {\n        color: var(--success);\n      }\n\n      .vs__actions:after {\n        color: var(--success);\n      }\n    }\n\n    &.warning {\n      border: solid 1px var(--warning);\n      input, .selected {\n        color: var(--warning);\n      }\n\n      .vs__actions:after {\n        color: var(--warning);\n      }\n    }\n\n    &.error {\n      border: solid 1px var(--error);\n\n      > label {\n        color: var(--error);\n      }\n\n      .vs__actions:after {\n        color: var(--error);\n      }\n    }\n  }\n}\n\n@mixin form-focus {\n  // Focus for form like elements (not to be confused with basic :focus style)\n  outline: none;\n  border-color: var(--outline);\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n.title-block {\n  display: flex;\n  justify-content: right;\n  align-items: center;\n\n  h1 {\n    margin: 0 !important;\n  }\n\n  div {\n    display: flex;\n    align-items: center;\n  }\n}\n\ntable {\n  border-collapse: collapse;\n  overflow: hidden;\n  border-radius: var(--border-radius);\n\n  &.full-width {\n    border-radius: 0 !important;\n    min-width: 60%;\n\n    thead {\n      cursor: pointer;\n\n      th div {\n          display: flex;\n          align-items: center;\n          justify-content: space-between;\n      }\n    }\n\n    tbody {\n      border-bottom: 1px solid var(--sortable-table-top-divider);\n    }\n  }\n\n  tr > td:first-of-type {\n    width: 20%;\n  }\n\n  th, td {\n    border: 1px solid var(--border);\n    padding: 8px 5px;\n    min-width: 150px;\n    text-align: left;\n  }\n\n  th {\n    background-color: var(--sortable-table-top-divider);\n    border-bottom: 1px solid var(--sortable-table-header-bg);\n  }\n\n  a {\n    cursor: pointer;\n  }\n\n  .os {\n    display: flex;\n    align-items: center;\n  }\n}\n\n.logs-container {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n  background-color: #FFF;\n  border: 1px solid var(--body-text);\n  height: 300px;\n  overflow-x: hidden;\n  overflow-y: auto;\n\n  li {\n    font-family: $mono-font;\n    font-size: 12px;\n    margin: 0;\n    padding: 10px 20px;\n    border-bottom: 1px solid #ccc;\n\n    &.log {\n      color: #000;\n    }\n\n    &.warn {\n      background-color: LightGoldenRodYellow;\n      color: SaddleBrown;\n    }\n\n    &.info {\n      background-color: Azure;\n      color: blue;\n    }\n\n    &.error {\n      background-color: MistyRose;\n      color: red;\n    }\n\n    &:last-child {\n      border-bottom: none;\n    }\n\n    .log-entry-type {\n      font-weight: bold;\n    }\n  }\n}\n\n.resources-count-container {\n  .cluster-row {\n    display: grid;\n    grid-template-columns: repeat(4, 1fr) 20px;\n    grid-template-rows: 1fr;\n    align-content: center;\n    font-weight: normal;\n  }\n\n  tbody tr, tbody tr th {\n    background-color: var(--sorta